<template>
  <div class="wrapper">
      <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
  <el-breadcrumb-item :to="{ path: '/home/wellcom' }">首页</el-breadcrumb-item>
  <el-breadcrumb-item>用户管理</el-breadcrumb-item>
  <el-breadcrumb-item>用户列表</el-breadcrumb-item>
</el-breadcrumb>
<!-- 卡片视图  -->
<el-card>
    <!-- 添加角色 -->
   <el-row>
      <el-col>
  <el-button type="primary">添加角色</el-button>
      </el-col>
   </el-row>
   <!-- 角色列表区域 -->
     <el-table :data="roleslist" border stripe >
            <el-table-column  type="expand" >
               <template slot-scope="scope">
                  <el-row :class="['bottom-bor',index===0?'top-bor':'','vscode']" v-for="(item,index) in scope.row.children" :key="item.id"  >
                      <!-- 渲染一级 -->
                      <el-col :span="5">
                       <el-tag  closable @close="removeRolesId(scope.row,item.id)">
                           {{item.authName}}
                       </el-tag>
                       <i class="el-icon-caret-right"></i>
                      </el-col >
                      <!-- 渲染二级跟三级 -->
                      <el-col :span="19" >
                          <!-- 渲染二级嵌套权限 -->
                         <el-row :class="[index===0?'':'top-bor','vscode']" v-for="(items,index) in item.children" :key="index"  >
                      <el-col :span="6">
                        <el-tag type="success"  closable @close="removeRolesId(scope.row,items.id)">
                            {{items.authName}}
                        </el-tag> 
                          <i class="el-icon-caret-right"></i>  
                      </el-col>
                       <!-- 渲染三级 -->
                       <el-col :span="18">
                        <el-tag v-for="(itemss) in items.children" :key="itemss.id" type="warning"   closable  @close="removeRolesId(scope.row,itemss.id)">
                            {{itemss.authName}}
                        </el-tag>
                      </el-col>
                      </el-row>


                      </el-col>
                  </el-row>
               </template>

            </el-table-column>
        <el-table-column    type="index" label="#"></el-table-column>
        <el-table-column    label="角色名称" prop="roleName"></el-table-column>
        <el-table-column    label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column    label="操作"  width="300px">
            <template slot-scope="scope">
                  <el-button type="primary" icon="el-icon-search" size='mini'>编辑</el-button>
                  <el-button type="danger" icon="el-icon-search" size='mini'>删除</el-button>
                  <el-button type="warning" icon="el-icon-search" size='mini' @click="showSetDailog(scope.row)">分配权限</el-button>
            </template>
        </el-table-column>
    </el-table>
</el-card>
   <!-- 权限框的展示 -->
   <el-dialog
  title="分配权限"
  :visible.sync="dialogVisibleRoles"
  width="50%"
  @close="RolesDialogClosed"
   >
 <el-tree :data="RoleStatelist" :props="treeProps" default-expand-all   show-checkbox node-key="id" :default-checked-keys="defaultKeys" ref="treeRef"></el-tree>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisibleRoles = false">取 消</el-button>
    <el-button type="primary" @click="allotRights">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      // 权限列表数据
        roleslist:[],
        // 控制显示对话框的显示与隐藏
        dialogVisibleRoles:false,
        // 分配权限列表数据
        RoleStatelist:[],
        // 树形控件
        treeProps:{
          label:'authName',
          children:'children'
        },
        // 默认选中的节点Id数组值
        defaultKeys:[],
          // 获取权限分配的id
          RolesId:''
    };
  },
  watch: {},
  computed: {},
  methods: {
    async  getRoles(){
         
    let {data:res}=await this.$http.get('roles')
    if(res.meta.status!==200){
          this.$message.error(res.meta.msg)
    }else{
            this.$message.success(res.meta.msg)
    }
      this.roleslist=res.data
      console.log(this.roleslist)
      },
      // 根据id删除对应的三级权限
    async  removeRolesId(role,rightId){
       console.log(role)
      let confirm=await  this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err=>err)
            if(confirm!=='confirm'){
                return  this.$message.info('用户取消了删除')
            }else{
                 this.$message.success('确认了删除')
            }
      let {data:res}=await this.$http.delete(`roles/${role.id}/rights/${rightId}`)

          console.log(res)
              if(res.meta.status!==200){
                this.$message.error('删除权限失败')
              }else{
                this.$message.success('删除权限成功')
              }
           role.children=res.data
       },
      //  分配权限框显示
     async showSetDailog(role){
        this.RolesId=role.id
         let {data:res}= await this.$http.get('rights/tree')
          console.log(res)
         if(res.meta.status!==200){
             this.$message.error(res.meta.msg)
         }else{
            this.$message.success(res.meta.msg)
         } 
        //  获取分配权限数据
            this.RoleStatelist=res.data
             console.log(this.RoleStatelist)
              // 获取三级节点的id
              this.getDefaKyes(role,this.defaultKeys)
              this.dialogVisibleRoles=true
      },
      //  递归函数
       getDefaKyes(node,arr){
         if(!node.children){
           return arr.push(node.id)
         }
         node.children.forEach(item=>{
           this. getDefaKyes(item,arr)
         })
       },
      //  监听分配对话框的关闭事件
      RolesDialogClosed(){
          this.defaultKeys=[]
      },
      // 分配权限函数
    async  allotRights(){
         let  keys=[
           ...this.$refs.treeRef.getCheckedKeys(),
            ...this.$refs.treeRef.getHalfCheckedKeys()
         ]  
         let idStr=keys.join(',')
       let {data:res}=await  this.$http.post(`roles/${this.RolesId}/rights`,{
           rids:idStr
         })
         console.log(res)
         if(res.meta.status!==200){
            this.$message.error(res.meta.msg)
         }else{
             this.$message.success(res.meta.msg)
         }
            this.getRoles()
           this.dialogVisibleRoles=false
         
      }
  },
  created() {
      this.getRoles()
 
  },
  mounted() {}
};
</script>
<style lang="less" scoped>
.el-tag{
    margin:7px;
}
.top-bor{
      border-top: 1px solid #eee;
}
.bottom-bor{
    border-bottom: 1px solid #eee;
}
.vscode{
  display: flex;
  align-items: center;
}
</style>