<template>
  <div class="wrapper">
      <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
  <el-breadcrumb-item :to="{ path: '/home/wellcom' }">首页</el-breadcrumb-item>
  <el-breadcrumb-item>用户管理</el-breadcrumb-item>
  <el-breadcrumb-item>用户列表</el-breadcrumb-item>
</el-breadcrumb>
 <!-- 卡片区域 -->
 <el-card class="box-card">
     <!-- 用户搜索区域 -->
     <el-row :gutter="20">
  <el-col :span="8"><div class="grid-content bg-purple">
      <el-input placeholder="请输入内容" clearable @clear="getUserlist" class="input-with-select" v-model="axiosUserlist.query">  <el-button slot="append" icon="el-icon-search" @click=" getUserlist"></el-button></el-input>
      </div></el-col>
  <el-col :span="4"><div class="grid-content bg-purple">
      <el-button type="primary" @click="dialogVisible=true">添加用户</el-button>
      </div></el-col>
 </el-row>
    <!-- 用户列表区域 -->
    <el-table :data="userlist" border stripe >
        <el-table-column    type="index" ></el-table-column>
        <el-table-column    label="姓名" prop="username"></el-table-column>
        <el-table-column    label="手机" prop="mobile"></el-table-column>
        <el-table-column    label="邮箱" prop="email"></el-table-column>
        <el-table-column    label="日期" prop="create_time"></el-table-column>
        <el-table-column    label="状态" prop="mg_state">
            <template slot-scope="scope">
              <el-switch v-model="scope.row.mg_state"   @change="userStateChanged(scope.row)"></el-switch>
            </template>
        </el-table-column>
        <el-table-column    label="角色" prop="role_name"></el-table-column>
         <el-table-column   label="操作"   width="180px">

           
          <template  slot-scope="scope">
                <!-- 修改按钮 -->
                <el-tooltip class="item" effect="dark" content="修改" placement="top-start" :enterable=false>
                   <el-button type="primary" icon="el-icon-edit" size="mini"  @click="addStatefn(scope.row.id)"></el-button>
                </el-tooltip>
              
                <!-- 删除按钮 -->
                    <el-tooltip class="item" effect="dark" content="删除" placement="top-start" :enterable=false>
                        <el-button type="warning" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.id)"></el-button>
                    </el-tooltip>
                  
                <!-- 分配角色按钮 -->
                <el-tooltip class="item" effect="dark" content="分配角色" placement="top-start" :enterable=false>
                     <el-button type="danger" icon="el-icon-share" size="mini" @click="setUserStatesFn(scope.row)"></el-button>
                    </el-tooltip>
          </template>
         </el-table-column>
    </el-table>
     <!-- 分页区域 -->
         
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page=" axiosUserlist. pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="axiosUserlist.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
</el-card>
    <!-- 添加用户的对话框 -->
    <el-dialog
  title="添加用户"
  :visible.sync="dialogVisible"
  width="50%"
  @close="removeCloseFn"
  >
  <!-- 内容主题区域 -->
    <el-form :model="ruleForm" :rules="FormRules" ref="ruleFormRef" label-width="70px" >
  <el-form-item label="用户名" prop="username">
    <el-input v-model="ruleForm.username"></el-input>
  </el-form-item>
  <el-form-item label="密码" prop="password">
    <el-input v-model="ruleForm.password"></el-input>
  </el-form-item>
  <el-form-item label="邮箱" prop="email">
    <el-input v-model="ruleForm.email"></el-input>
  </el-form-item>
  <el-form-item label="手机" prop="mobile">
    <el-input v-model="ruleForm.mobile"></el-input>
  </el-form-item>
    </el-form>
  <!-- 底部区域 -->
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="addUser">确 定</el-button>
  </span>
</el-dialog>
  <!-- 修改用户对话框 -->
<el-dialog
  title="提示"
  :visible.sync="dialogVisibleXg"
  width="50%"
   @close="removeCloseFnXg"
  >
  <el-form :model="userIdForm" :rules="userIdrules" ref="userIdruleForm" label-width="100px" class="demo-ruleForm">
  <el-form-item label="用户名" prop="username">
    <el-input v-model="userIdForm.username"  disabled></el-input>
  </el-form-item>
  <el-form-item label="手机号" prop="mobile">
    <el-input v-model="userIdForm.mobile" ></el-input>
  </el-form-item>
  <el-form-item label="邮箱" prop="email">
    <el-input v-model="userIdForm.email"></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisibleXg = false">取 消</el-button>
    <el-button type="primary" @click="dialogVisibleStates">确 定</el-button>
  </span>
</el-dialog>
    <!-- 分配角色的对话框 -->
    <el-dialog
  title="分配角色"
  :visible.sync="setUsersdialogVisible"
  width="50%"
  >
  <!-- 用户信息 -->
  <div>
    <p>当前的用户:{{userInfo.username}}</p>
    <p>当前的角色:{{userInfo.role_name}}</p>
     <p>分配新的角色:
      <el-select v-model="selectvalueId" placeholder="请选择">
    <el-option
      v-for="item in roleslist"
      :key="item.id"
      :label="item.roleName"
      :value="item.id">
    </el-option>
  </el-select>
     </p>
  </div>
  <span slot="footer" class="dialog-footer">
    <el-button @click="setUsersdialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="setUserRolesFn()">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
      

        // 手机合法验证
      var checkMobile = (rule, value, cb) => {
            const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
            if (regMobile.test(value)) {
                // 合法的手机号码
                return cb() 
            }
            cb(new Error('手机号码格式不正确'))
        };
        // 邮箱合法验证
        var checkEmail = (rule, value, cb) => {
            const regEmail = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
            if (regEmail.test(value)) {
                // 合法的邮箱
                return cb() 
            }
            cb(new Error('请输入合法的邮箱'))
        };
    return {
        // 控制对话框的显示影藏
       dialogVisibleXg:false,
        // 添加用户列表的数据
        axiosUserlist:{
            // 搜索输入
             query :'',
              //  当前页数
             pagenum:1,
            //  当前显示多少数据
             pagesize :2
           
        },
        // 角色分配对话框显示与影藏
        setUsersdialogVisible:false,
        // 需要被分配角色的用户信息
         userInfo:{},
        //  获取需要分配的角色
        roleslist:[],
        // 已选中的角色Id值
        selectvalueId:'',
        // 获取列表数据
      userlist:'',
      total:0,
    //   控制对话框显示与隐藏
      dialogVisible:false,
    //   添加数据
      ruleForm:{
          username:'',
          password:'',
          mobile:'',
          email:''
      },
    //   添加表单的验证
      FormRules:{
          username: [
            { required: true, message: '请输入用户名', trigger: 'blur' },
            { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
          ],
           password: [
          { required: true, message: '请输入用户密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ],
        email: [
           { required: true, message: '请输入邮箱', trigger: 'blur' },
            {validator:checkEmail, trigger: 'blur' }
        ],
        mobile: [
            { required: true, message: '请输入手机号码', trigger: 'blur' },
            { validator:checkMobile, trigger: 'blur' }
        ]
      },
      //  修改数据
       userIdForm:{},
      //  修改表单验证规则
       userIdrules:{
             username: [
            { required: true, message: '请输入用户名', trigger: 'blur' },
            { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
          ],
        email: [
           { required: true, message: '请输入邮箱', trigger: 'blur' },
            {validator:checkEmail, trigger: 'blur' }
        ],
        mobile: [
            { required: true, message: '请输入手机号码', trigger: 'blur' },
            { validator:checkMobile, trigger: 'blur' }
        ]

       }
    };
  },
  watch: {},
  computed: {},
  methods: {
    //   获取用户列表的数据
     async  getUserlist(){
            let {data:res}= await this.$http.get('users',{
                params:this.axiosUserlist
            })
            if(res.meta.status!==200) return this.$massage.error(res.meta.msg)
             this.userlist=res.data.users,
             this.total=res.data.total
            console.log(res)
       },
    //    监听pagesize
       handleSizeChange(newsize){
             console.log(newsize)
             this.axiosUserlist.pagesize=newsize
             this.getUserlist()
       },
    //    监听页码的改变
    handleCurrentChange(newpage){
           console.log(newpage)
            this.axiosUserlist.pagenum=newpage
             this.getUserlist()
    },
    // 监听swic开关状态的改变
  async  userStateChanged(userinfo){
         console.log(userinfo)
          // 获取userinfo值储存到链接进行同步
       let {data:res}=await  this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
         console.log(res)
         if(res.meta.status!==200){
             userinfo.mg_state=!userinfo.mg_state
             this.$message.error(res.meta.msg)
         }else{
             this.$message.success(res.meta.msg)
         }
         
    },
    // 点击取消按钮进行重置添加
    removeCloseFn(){
        this.$refs.ruleFormRef.resetFields()
    },
     // 点击取消按钮进行重置修改
    removeCloseFnXg(){
        this.$refs.userIdruleForm.resetFields()
    },
    // 添加用户
  async  addUser(){
   
        this.$refs.ruleFormRef.validate(async valid => {
         
             if(!valid) return
              // 获取用户信息上传
             let {data:res}=await this.$http.post('users',this. ruleForm) 
             console.log(res)
                   if(res.meta.status!==201){
                       this.$message.error(res.meta.msg)
                   }else{
                      this.$message.success(res.meta.msg)
                   }
                   this.dialogVisible=false
                     this.getUserlist()
             })
    },
    // 拿到用户信息
  async  addStatefn(id){
         this.dialogVisibleXg=true
         let {data:res}= await this.$http(`users/${id}`)
          console.log(res)
          if(res.meta.status!==200){
              this.$message.error(res.meta.msg)
          }else{
              this.$message.success(res.meta.msg)
              this.userIdForm=res.data
          }
 
    },
    // 修改用户信息
    dialogVisibleStates(){
         this.$refs.userIdruleForm.validate(async valid => {
         
             if(!valid) return
              // 获取用户信息上传
             let {data:res}=await this.$http.put ('users/'+this.userIdForm.id,{
               mobile:this.userIdForm.mobile,
               email:this.userIdForm.email
             }) 
             console.log(res)
                   if(res.meta.status!==200){
                       this.$message.error(res.meta.msg)
                   }else{
                        this.$message.success(res.meta.msg)
                   }
                   this. dialogVisibleXg=false
                     this.getUserlist()
             })


    },
    // 根据id删除用户列表数据
 async removeUserById(id){
        // 弹框询问用用户是否删除数据
    let confirm= await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err=>err)
           if(confirm!=='confirm'){
            return   this.$message.info('已取消删除')
           }
        //  删除id对应的用户
      let {data:res} = await this.$http.delete(`users/${id}`)
        if(res.meta.status!==200){
            this.$message.error(res.meta.msg)
        }else{
            this.$message.success(res.meta.msg)
            this.getUserlist()
        }
             
  },
    //  用户页面分配角色按钮
   async setUserStatesFn(userInfo){
      this.userInfo=userInfo
    
      //  展示对话框之前展示所有的列表
    let {data:res}=await this.$http.get('roles')
    console.log(res)
      if(res.meta.status!==200){
         this.$message.error(res.meta.msg)
      }else{
        this.$message.success(res.meta.msg)
      }
      this.roleslist=res.data
      this.setUsersdialogVisible=true
    },
    // 点击按钮分配角色
  async  setUserRolesFn(){
       if(!this.selectvalueId){
         return  this.$message.error('请选择分配的角色!')
       }
    let {data:res}= await this.$http.put(`users/${this.userInfo.id}/role`,{
         rid:this.selectvalueId
    })
    if(res.meta.status!==200){
        this.$message.error(res.meta.msg)
    }else{
        this.$message.success(res.meta.msg)
    }
      this.getUserlist()
      this.setUsersdialogVisible=false
      console.log(res)
    }
  },
  created() {
       this.getUserlist()
  },
  mounted() {},
  
};

</script>
<style lang="less" scoped>
.wrapper{}
</style>