<template>
  <div class="wrapper">
    <!-- 布局容器 -->
    <el-container class="home-container">
      <!-- 头部区域 -->
      <el-header
        >
        <div>
          <img src="../assets/heima.png" alt="">
          <span>电商后台管理系统</span>
        </div>
         <el-button type="info" @click="delefn">退出登陆</el-button></el-header
      >
      <!-- 页面主体区域 -->
      <el-container>
        <!-- 侧边栏 -->
        <el-aside :width="activefn?'64':'200px'" >
          <div class="toggle_button" @click="toggleFn">
            |||
          </div>
          <!-- 侧边栏菜单区域 -->
      <el-menu :default-openeds="['1', '3']"  text-color="#ccc" height="100%" unique-opened :collapse="activefn" :collapse-transition="false" router :default-active="activePath">
        <!-- 一级菜单 -->
      <el-submenu :index="item.id+''" v-for="(item) in menuslist" :key="item.id">
        <!-- 一级菜单模板区域 -->
        <template slot="title">
          <!-- 图标 -->
          <i :class="iconImage[item.id]" class="iconImage"></i>
          <!-- 文本 -->
         <span >{{item.authName}}</span>
         </template>
           <!-- 二级菜单 -->
          <el-menu-item :index="'/'+itemlist.path" v-for="(itemlist) in item.children" :key="itemlist.id" @click="activeNav('/'+itemlist.path)">
             <!-- 二级菜单模板区域 -->
        <template slot="title">
          <!-- 图标 -->
          <i class="el-icon-menu"></i>
          <!-- 文本 -->
          <span>{{itemlist.authName}}</span></template>
          </el-menu-item>
        </el-submenu>
     
    </el-menu>
        </el-aside>
        <!-- 右侧内容主体区域 -->
        <el-main>
          <!-- 路由占位符 -->
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
   data() {
   return {
     menuslist:[],
      // 配置相应的菜单图标
     iconImage:{
        '125':'iconfont icon-user',
        '103':'iconfont icon-tijikongjian',
        '101':'iconfont icon-shangpin',
        '102':'iconfont icon-danju',
        '145':'iconfont icon-baobiao'
     },
    //  是否折叠
     activefn:false,
    //  字体高亮的链接
     activePath:''
   }
  },
  watch: {},
  computed: {},
  methods: {
      //  退出登陆
    delefn() {
      window.sessionStorage.clear('token')
      this.$router.push('/login')
    },
    // 获取所有菜单
   async axiosFn(){
         let {data:res}= await this.$http.get('menus')
          if(res.meta.status!==200) return this.$message.error(res.meta.msg)
           this.menuslist=res.data
         console.log(res)
    },
    // 点击折叠
    toggleFn(){
       this.activefn=!this.activefn
    },
    // 动态绑定字体高亮
    activeNav(activePath){
       window.sessionStorage.setItem("activepath",activePath)
       this.activePath=activePath
    }
  },
  created() {
    this.axiosFn(),
    this.activePath=window.sessionStorage.getItem("activepath")
  },
  mounted() {}
}
</script>
<style lang="less" scoped>
.el-header{
  background-color:#373d41;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #fff;
  font-size: 20px;
  > div{
   display: flex;
   align-items: center;
   span{
     margin-left: 20px;
   }
  }
}
.el-aside{
  background-color:#333744;
  height:100%
}
.el-main{
  background: #ccc;
}
.wrapper,.home-container{
   height: 100%;
}
.iconImage{
  margin-right: 15px;
}
.toggle_button{
  background: #4A5064;
  height: 24px;
  color: #fff;
  text-align: center;
  line-height: 24px;
  font-size: 10px;
  letter-spacing: 0.2em;
  cursor: pointer;
}
 .el-menu{
  border-right: none;
  background-color: #333744   ! important;
}
</style>
