<template>
  <div class="wrapper">
        <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
  <el-breadcrumb-item :to="{ path: '/home/wellcom' }">首页</el-breadcrumb-item>
  <el-breadcrumb-item>商品管理</el-breadcrumb-item>
  <el-breadcrumb-item>商品分类</el-breadcrumb-item>
</el-breadcrumb>
<!-- 卡片区域 -->
 <el-card>
  <el-row>
      <el-col>
          <el-button type="primary" @click="setCateFn()">添加分类</el-button>
      </el-col>
  </el-row>
  <!-- 表格区 -->
  <tree-table :data="catelist" :columns="columns" :selection-type="false" :expand-type="false"  :show-index="true"  index-text="#"	border   class="box-table">
  <template slot="valid" slot-scope="scope">
      <i class="el-icon-success" v-if="scope.row.cat_deleted===false" style="color:lightgreen"></i>
      <i class="el-icon-error" v-else  style="color:red"></i>
  </template>
  <template slot="level" slot-scope="scope">
        <el-tag v-if="scope.row.cat_level==0">标签一</el-tag>
        <el-tag  v-else-if="scope.row.cat_level==1" type="success">标签二</el-tag>
        <el-tag  v-else type="warning">标签三</el-tag>
  </template>
  <template  slot="solet" slot-scope="scope">
       <el-button type="primary" icon="el-icon-edit"   @click="addCatefn(scope.row.cat_id)"></el-button>
       <el-button type="danger" icon="el-icon-delete"  @click="removeCate(scope.row.cat_id)"></el-button>
  </template>
  </tree-table>
  <!-- 分页区 -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="querInfo. pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="querInfo. pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total=" total">
    </el-pagination>
</el-card>
  <!-- 添加分类的对话框 -->
  <el-dialog
  title="提示"
  :visible.sync="dialogVisibleCate"
  width="50%"
  @close="addCateDialogFn()"  >
  <!-- 添加分类的表单 -->
<el-form :model="ruleForm" :rules="rules" ref="ruleForms" label-width="100px" >
  <el-form-item label="分类名称" prop="cat_name">
    <el-input v-model="ruleForm.cat_name"></el-input>
  </el-form-item>
   <el-form-item label="父级分类" >
       <!-- options用来指定数据源 -->
        <!-- props用来指定数据对象 -->
<el-cascader 
    expand-trigger="hover"
    v-model="selectKeys"
    :options="parentCatelist"
    :props="expandTriggers"
    @change="handleChange" clearable change-on-select></el-cascader>
  </el-form-item>
 </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisibleCate = false">取 消</el-button>
    <el-button type="primary" @click="addCate">确 定</el-button>
  </span>
</el-dialog>
   <!-- 修改用户对话框 -->
<el-dialog
  title="提示"
  :visible.sync="dialogVisibleXg"
  width="50%"
   @close="removeCloseFnXg"
  >
  <el-form :model="CateIdForm" :rules="CateIdrules" ref="CateIdruleForm" label-width="100px" class="demo-ruleForm">
  <el-form-item label="用户名" prop="cat_name">
    <el-input v-model="CateIdForm.cat_name"  ></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisibleXg = false">取 消</el-button>
    <el-button type="primary" @click="dialogVisibleStates">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      //  修改数据
        CateIdForm:{},
      // 控制对话框的显示影藏
       dialogVisibleXg:false,
      //    修改表单验证规则
     CateIdrules:{
            Cat_name: [
            { required: true, message: '请输入用户名', trigger: 'blur' },
          ]
       },
        // 查询条件
        querInfo:{
            type:"",
          pagenum:3,
          pagesize:5
        },
        // 获取商品分类的数据
     catelist:[],
    //  总条数数据
     total:0,
     columns:[
         {
         label:'分类名称',
          prop:'cat_name'
         },
         {
         label:'是否有效',
         type:'template',
         template:'valid'
         },
          {
         label:'排序',
         type:'template',
         template:'level'
         },
          {
         label:'操作',
         type:'template',
         template:'solet',
         with:"auto",
         align:"center",
         minWidth:"60px"
         
         }
       ],
    //    控制对话框的显示隐藏
       dialogVisibleCate:false,
    //    父级分类列表
        parentCatelist:[],
        // 指定级联选择器的配置对象
         expandTriggers:{
               value: 'cat_id',
              label: 'cat_name',
              children:'children',
              expandTrigger:'hover'
         },
        //  父类数据双向绑定
         selectKeys:[],
    //    添加分类的表单对象
       ruleForm:{
        cat_name:'',
    //   父级分类的id
       cat_pid:0,
    //    分类的等级 默认为一级分类
        cat_level:0
       },
    //    添加表单验证规则对象
       rules:{
       cat_name:[
          { required: true, message: '请输入分类的名称', trigger: 'blur' },
             ]
       }
    };
  },
  watch: {},
  computed: {},
  methods: {
 // 拿到用户信息
  async  addCatefn(id){
    console.log(id)
         this.dialogVisibleXg=true
         let {data:res}= await this.$http.get("categories/"+id)
          console.log(res)
          if(res.meta.status!==200){
              this.$message.error(res.meta.msg)
          }else{
              this.$message.success(res.meta.msg)
              this.CateIdForm=res.data
          }
 
    },
      // 修改用户信息
    dialogVisibleStates(){
         this.$refs.CateIdruleForm.validate(async valid => {
         
             if(!valid) return
              // 获取用户信息上传
             let {data:res}=await this.$http.put(`categories/${this.CateIdForm.cat_id}`,{
             cat_name:this.CateIdForm.cat_name,
    
             }) 
             console.log(res)
                   if(res.meta.status!==200){
                       this.$message.error(res.meta.msg)
                   }else{
                        this.$message.success(res.meta.msg)
                   }
                   this. dialogVisibleXg=false
                    this.getCateFn()   
             })


    },


     // 点击取消按钮进行重置修改
      removeCloseFnXg(){
        this.$refs.CateIdruleForm.resetFields()
    },
    //   点击添加商品
     async   getCateFn(){
        let {data:res}= await this.$http.get('categories/',{params:this.querInfo})
          console.log(res)
           if(res.meta.status!==200){
               this.$message.error(res.meta.msg)
           }else{
               this.$message.success(res.meta.msg)
           }
        //    获取商品数据
        this.catelist=res.data.result
        // 为总条数数据赋值
        this.total=res.data.total
        },
        // 监听pagesize的改变
        handleSizeChange(newSize){
         this. querInfo. pagesize=newSize
         this. getCateFn()
        },
        // 监听pageNum的改变
        handleCurrentChange(newNum){
          this. querInfo. pagenum=newNum
            this. getCateFn()
        },
        // 添加分类
        setCateFn(){
       
           this.dialogVisibleCate=true
           this.getParentCatelist()
        },
        // 获取父级分类的列表
     async   getParentCatelist(){
            let {data:res}= await this.$http.get('categories/',{
                 params:{
                     type:2
                 }
            })
             if(res.meta.status!==200){
               this.$message.error(res.meta.msg)
             }else{
                this.$message.success(res.meta.msg)
             }
             console.log(res)
            this.parentCatelist=res.data
        },
        // 选择项触发这个函数
        handleChange(){
         if(this.selectKeys.length>0){
            //  父级分类的id
            this.ruleForm.cat_pid=this.selectKeys[
                this.selectKeys.length-1
            ]  
            // 为当前种类的赋值
            this.ruleForm.cat_level=this.selectKeys.length
            return
         }else{
              this.ruleForm.cat_pid=0
               // 为当前种类的赋值
            this.ruleForm.cat_level=0
         }
            
        },
        // 点击按钮添加新的分类
          addCate(){
         
         this.$refs.ruleForms.validate ( async valid=>{
               if(!valid) return
              let {data:res}= await this.$http.post('categories/',this.ruleForm)
             if(res.meta.status!==201){
                 this.$message.error(res.meta.msg)
             }else{
                 this.$message.success(res.meta.msg)
             }
               this.dialogVisibleCate=false
           })
              },
    //    监听对话框的关闭事件
        addCateDialogFn(){
         this.$refs.ruleForms.resetFields()
         this.selectKeys =[]
          this.ruleForm.cat_level=0
          this.ruleForm.cat_pid=0
        },
          // 根据id删除用户列表数据
 async removeCate(id){
     console.log(id)
        // 弹框询问用用户是否删除数据
    let confirm= await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err=>err)
           if(confirm!=='confirm'){
            return   this.$message.info('已取消删除')
           }
        //  删除id对应的用户
      let {data:res} = await this.$http.delete(`categories/${id}`)
        if(res.meta.status!==200){
            this.$message.error(res.meta.msg)
        }else{
            this.$message.success(res.meta.msg)
            
        }
         this.getCateFn()     
  },

  },
  created() {
      this.getCateFn()
   
  },
  mounted() {}
};
</script>
<style lang="less" scoped>
.box-table{
    margin-top: 15px;
}
.el-cascader{
    width: 100%;
}
</style>