<template>
  <div class="wrapper">
     <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
  <el-breadcrumb-item :to="{ path: '/home/wellcom' }">首页</el-breadcrumb-item>
  <el-breadcrumb-item>商品管理</el-breadcrumb-item>
  <el-breadcrumb-item>参数列表</el-breadcrumb-item>
</el-breadcrumb>
<!-- 卡片区域 -->
<el-card>
      <!-- 警告提示区域 -->
    <el-alert
    title="警告:只允许为第三级分类设置相关参数!"
    type="warning"
    :closable="false"
    >
  </el-alert>
  <!-- 选择商品分类区域 -->
  <el-row class="cat_top">
      <el-col>
          <span>选择商品分类:</span>
          <!-- 选择商品分类的级联选择框 -->
          <el-cascader 
    expand-trigger="hover"
    v-model="selectKeys"
    :options=" Catelist"
    :props="expandTriggers"
    @change="handleChange" clearable change-on-select></el-cascader>
      </el-col>
  </el-row>
  <!-- tap页签区域 -->
  <el-tabs v-model="activeName" @tab-click="handleClick">
      <!-- 添加动态参数的面板 -->
    <el-tab-pane label="动态参数" name="many">
        <!-- 添加动态参数的按钮 -->
          <el-button type="primary" size="mini" :disabled=" isBtndisbol" @click="dialogVisibleShow=true">添加参数</el-button>
          <!-- 动态的参数表格 -->
          <el-table :data="manylist"
                    style="width: 100%" border stripe>
                    <!-- 展开行 -->
                     <el-table-column type="expand" > </el-table-column>
                    <!-- 索引列 -->
            <el-table-column type="index" label="#"> </el-table-column>
            <el-table-column label="参数" prop="attr_name">
               
            </el-table-column>
               <el-table-column label="操作">
                    <template slot-scope="scope">
                    <el-button type="primary" size="mini" icon="el-icon-edit" @click="showditeDialog(scope.row.attr_id)">编辑</el-button>
                 <el-button type="danger" size="mini" icon="el-icon-delete"  @click="removeCate(scope.row.attr_id)">删除</el-button>
                </template>
                    </el-table-column>
          </el-table>
    </el-tab-pane>
       <!-- 添加静态参数的面板 -->
    <el-tab-pane label="静态属性" name="only">
        <!-- 添加静态参数的按钮 -->
          <el-button type="primary" size="mini" :disabled=" isBtndisbol" @click="dialogVisibleShow=true">添加属性</el-button>
            <!-- 静态的参数表格 -->
          <el-table :data="onlylist"
                    style="width: 100%" border stripe>
                     <!-- 展开行 -->
             <el-table-column type="expand" > </el-table-column>
                    <!-- 索引列 -->
            <el-table-column type="index" label="#"> </el-table-column>
            <el-table-column label="属性名称" prop="attr_name">
               
            </el-table-column>
               <el-table-column label="操作">
                    <template slot-scope="scope">
                    <el-button type="primary" size="mini" icon="el-icon-edit" @click="showditeDialog(scope.row.attr_id)">编辑</el-button>
                 <el-button type="danger" size="mini" icon="el-icon-delete" @click="removeCate(scope.row.attr_id)">删除</el-button>
                </template>
                    </el-table-column>
          </el-table>
    </el-tab-pane>
  </el-tabs>
</el-card>
<!-- 添加参数的对话框 -->
<el-dialog
  :title="'添加'+titleText"
  :visible.sync="dialogVisibleShow"
  width="50%"
  @close="addParamsFn">
  <el-form :model="addruleForm" :rules="rules" ref="ruleForms" label-width="100px" class="demo-ruleForm">
  <el-form-item  :label="titleText" prop="attr_name">
    <el-input v-model="addruleForm.attr_name" ></el-input>
  </el-form-item>
</el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisibleShow = false">取 消</el-button>
    <el-button type="primary" @click="addparamsFn">确 定</el-button>
  </span>
</el-dialog>
<!-- 参数的对话框 -->
<el-dialog
  :title="'修改'+titleText"
  :visible.sync="editdialogVisibleShow"
  width="50%"
  @close="editParamsFn">
  <el-form :model="editruleForm" :rules="editrules" ref="editruleForms" label-width="100px" class="demo-ruleForm">
  <el-form-item  :label="titleText" prop="attr_name">
    <el-input v-model="editruleForm.attr_name" ></el-input>
  </el-form-item>
</el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editdialogVisibleShow = false">取 消</el-button>
    <el-button type="primary" @click="editparamsFn">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
        // 所有商品分类的数据
        Catelist:[],
        // 指定级联选择器的配置对象
         expandTriggers:{
               value: 'cat_id',
              label: 'cat_name',
              children:'children',
              expandTrigger:'hover'
         },
          //  父类数据双向绑定
         selectKeys:[],
          //    添加分类的表单对象
        //   被激活页签的名称
        activeName:'many',
       ruleForm:{
        cat_name:'',
    //   父级分类的id
       cat_pid:0,
    //    分类的等级 默认为一级分类
        cat_level:0
       },
       editruleForm:{
        cat_name:'',
    //   父级分类的id
        cat_pid:0,
    //    分类的等级 默认为一级分类
        cat_level:0

       },
    //    动态的参数数据
       manylist:[],
    //    静态的参数数据
       onlylist:[],
    //    控制对话框的显示隐藏
    dialogVisibleShow:false,
    // 验证规则
    rules:{
      attr_name:[
              { required: true, message: '请输入分类的名称', trigger: 'blur' },

      ]
    },
    editrules:{
        attr_name:[
              { required: true, message: '请输入分类的名称', trigger: 'blur' },

      ]
    },
    // 数据双向绑定
    addruleForm:{
        attr_name:''
    },
    editdialogVisibleShow:false
    };
  },
  watch: {},
  computed: {
    //   如果按钮被禁用则返回true ,否则返回false
    isBtndisbol(){
         if(this.selectKeys.length!==3){
              return true
         } 
              return false
    },
//   当前选中分类的id
    cateId(){
        if(this.selectKeys.length==3){
         return   this.selectKeys[2]
        }
        return null
    },
    // 动态计算标题的文本
    titleText(){
        if(this.activeName==='many'){
           return "动态参数"
        }
              return "静态属性"
        
    }
  },
  methods: {
    //   获取所有的商品分类
   async getCatelist(){
     let {data:res}= await this.$http.get('categories')
        if(res.meta.status!==200){
            this.$message.error(res.meta.msg)
        }else{
            this.$message.success(res.meta.msg)
        }
        this.Catelist=res.data
    },
      // 选择项触发这个函数
       handleChange(){
          this. getparamsDate()
        },
        //  页签点击事件的处理函数
         handleClick(){
            this.getparamsDate()
         },
        //  获取参数的列表数据
      async   getparamsDate(){
   // 证明你选中的不是三级分类
         if(this.selectKeys.length!==3){
          
          this.selectKeys=[]
          return
         }
    //   证明选中的是三级分类
           let {data:res}= await this.$http.get(`categories/${this.cateId}/attributes`,{
               params:{
                    sel:this.activeName
               }
           })
           console.log(res)
            if(res.meta.status!==200){
                this.$message.error(res.meta.msg)
            }else{
                this.$message.success(res.meta.msg)
            }
            console.log(res.data)
            if(this.activeName==='many'){
               this.manylist=res.data
            }else if(this.activeName==='only'){
                this.onlylist=res.data
            }
        },
        // 监听对话框的关闭事件
        addParamsFn(){
             this.$refs.ruleForms.resetFields()
        },
        // 点击确定按钮添加参数
        addparamsFn(){
               this.$refs.ruleForms.validate ( async valid=>{
               if(!valid) return
              let {data:res}= await this.$http.post(`categories/${this.cateId}/attributes`,{
                     attr_name:this.addruleForm.attr_name,
                     attr_sel:this.activeName

              })
             if(res.meta.status!==201){
                 this.$message.error(res.meta.msg)
             }else{
                 this.$message.success(res.meta.msg)
             }
                  this.getparamsDate()
               this.dialogVisibleShow=false
           })
        },
        // 修改
      async  showditeDialog(id){
         
         let {data:res}= await this.$http.get(`categories/${this.cateId}/attributes/${id}`,{
             params:{
                 attr_sel:this.activeName
             }
         })
          console.log(res)
          if(res.meta.status!==200){
              this.$message.error(res.meta.msg)
          }else{
              this.$message.success(res.meta.msg)
              this.editruleForm=res.data
          }
                 this.getparamsDate()
            this.editdialogVisibleShow=true
        },
        editParamsFn(){
            this.$refs.editruleForms.resetFields()
        },
        // 确认修改按钮
        editparamsFn(){
          this.$refs.editruleForms.validate ( async valid=>{
               if(!valid) return
              let {data:res}= await this.$http.put(`categories/${this.cateId}/attributes/${this.editruleForm.attr_id}`,{
                     attr_name:this.editruleForm.attr_name,
                     attr_sel:this.activeName

              })
             if(res.meta.status!==201){
                 this.$message.error(res.meta.msg)
             }else{
                 this.$message.success(res.meta.msg)
             }
                  this.getparamsDate()
               this.editdialogVisibleShow=false
           })
        },
              // 根据id删除用户列表数据
 async removeCate(id){
     console.log(id)
        // 弹框询问用用户是否删除数据
    let confirm= await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err=>err)
           if(confirm!=='confirm'){
            return   this.$message.info('已取消删除')
           }
        //  删除id对应的用户
      let {data:res} = await this.$http.delete(`categories/${this.cateId}/attributes/${id}`)
        if(res.meta.status!==200){
            this.$message.error(res.meta.msg)
        }else{
            this.$message.success(res.meta.msg)
            
        }
          this.getparamsDate()   
  },
  },
  created() {
      this.getCatelist()
  },
  mounted() {}
};
</script>
<style lang="less" scoped>
.cat_top{
    margin: 15px 0px;
}
</style>